body {
    // font-family: 'HelveticaNeueCyr'
    // font-weight: 300
    // font-variant: normal
    // font-size: 16px
    font-family: 'Arimo', sans-serif;
    font-weight: 400;
    font-style: normal;
    font-size: 100%; }

p {
    line-height: 22px; }

.page {
  width: 100%; }

.wrapper {
  width: 100%;
  height: auto;
  margin: 0 auto;
  background-color: #ccc;

  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  flex-direction: column;
  align-items: flex-start; }

.frame {
    width: 86.74963396778917%;
    margin: 0 auto;
    padding: 0 15px;
    display: flex;
    @include old-screen {
        font-size: 90%;
        width: 100%;
        box-sizing: border-box;
        padding:  0 1.28205128205128%; }
    @include tablet {
        font-size: 90%;
        width: 100%;
        box-sizing: border-box;
        padding:  0 1.28205128205128%; } }

// media
@media screen and (min-width:1367px ) {
  .frame {
    width: 1280px; } }



