$main_color  :  #003399;
$red_color   :  #de271f;
$green_color :  #00CA19;
$text_color  :  #565656;
$back_color  :  #f5f5f5;
$wh          :  #ffffff;
$aside_c1    :  #F5F5F5;
$aside_c2    :  #FBFBFB;
$aside_c3    :  #D5D5D5;
$text_p      :  #434343;

$text_min    : 1em;
// 12px
$text_medium : 18px;
// 14px
$text_title  : 24px;
// 18px
$text_big    : 32px;
// 24px

