@mixin main_button {
    transition: 0.8s;
    background: $main_color;
    color: $wh;
    &:hover {
        background: $green_color;
        color: $wh; } }

@mixin main_title {
    font-size: $text_big;
    color: $text_color;
    text-transform: uppercase; }
